<template lang="html">
  <div class="song-list">
    <ul>
      <li class="song-item" @click="selectItem(song, index)" v-for="(song, index) in songs">
        <div class="rank" v-if="rank">
          <span :class="getRankCls(index)" v-text="getRankText(index)"></span>
        </div>
        <div class="content">
          <h2 class="name">{{song.name}}</h2>
          <p class="desc">{{getDesc(song)}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    songs: {
      type: Array,
      default: () => [],
    },
    rank: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    getDesc(song) {
      return `${song.singer}·${song.album}`;
    },
    selectItem(item, index) {
      this.$emit('select', item, index);
    },
    getRankCls(index) {
      return index < 3 ? `icon icon${index}` : 'text';
    },
    getRankText(index) {
      return index > 2 ? index + 1 : '';
    },
  },
};
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/variable";
  @import "~common/stylus/mixin";

  .song-list
    .song-item
      display: flex
      align-items: center
      box-sizing: border-box
      height: 64px
      font-size: $font-size-medium
      .rank
        flex: 0 0 25px
        width: 25px
        margin-right: 30px
        text-align: center
        .icon
          display: inline-block
          width: 25px
          height: 25px
          background-size: 25px 25px
          &.icon0
            bg-image('first')
          &.icon1
            bg-image('second')
          &.icon2
            bg-image('third')
        .text
          color: $color-theme
          font-size: $font-size-large
      .content
        flex: 1
        line-height: 20px
        overflow: hidden
        .name
          no-wrap()
          color: $color-text
        .desc
          no-wrap()
          margin-top: 4px
          color: $color-text-d
</style>



